.footer {
    position: relative;
    padding: 32px 0;
    z-index: 2;
    height: 600px;
    &__left,
    &__right {
        padding: 32px 16px;
    }
    &__left {
        width: 35%;
        font-weight: bold;
        color: #132B4F;
        @include breakpoint (mobile) {
            font-size: 21px;
            font-weight: bold;
        }
    }
    &__content {
        position: relative;
        z-index: 3;
        @include breakpoint (mobile) {
            text-align: center;
        }
    }
    &__bg,
    &__bg-2 {
        position: absolute;
        z-index: 1;
        left: 0;
        right: 0;
        top: 0;
        bottom: 0;
        img {
            width: 100%;
            height: 100%;
            object-fit: cover;
        }
    }
    &__bg-2 {
        z-index: 2;
        bottom: 0;
        top: initial;
        right: 0;
        left: 0;
        // top: initial;
        img {
            object-fit: contain;
        }
    }
    .text-right {
        color: #132B4F;
        @include breakpoint(mobile) {
            text-align: center !important;
        }
    }
    .float-right,
    .float-left {
        @include breakpoint (mobile) {
            float: initial;
            width: 100%;
            padding: 6px 0;
        }
    }
    ul {
        list-style-type: none;
        @include breakpoint(mobile) {
            margin: 0;
            padding: 0;
        }
        li {
            text-align: right;
            @include breakpoint(mobile) {
                text-align: center;
            }
            a {
                color: #132B4F;
            }
        }
    }
}

.copyright {
    background-color: #132B4F;
    color: white;
    // position: absolute;
    bottom: 0;
    left: 0;
    right: 0;
    z-index: 8;
    height: 75px;
    .container {
        background-color: #132B4F;
        padding: 24px 0;
        .float-left {
            @include breakpoint (mobile) {
                float: initial !important;
                background-color: #132B4F;
                padding: 0 32px;
                text-align: center;
            }
        }
        .float-right {
            @include breakpoint (mobile) {
                float: initial !important;
                background-color: #132B4F;
                text-align: center;
                margin-top: 22px;
            }
            a {
                i {
                    color: white;
                    font-size: 20px;
                    margin-left: 16px;
                }
            }
        }
    }
}