.top {
    height: 680px;
    &__title {
        width: 480px;
        // height: 200px;
        position: absolute;
        top: 230px;
        margin: 0 auto;
        display: block;
        z-index: 4;
        left: 50%;
        @include breakpoint (mobile) {
            width: 320px;
            top: 274px;
        }
        transform: translate(-50%, -50%);
        h1 {
            color: white;
            font-weight: bold;
            text-shadow: 0px 4px 4px rgba(0, 0, 0, 0.25);
            font-family: 'News706BoldBT', sans-serif;
        }
    }
    &__bg {
        position: relative;
        height: 680px;
        overflow-y: hidden;
        background: #E9C070;
        // background-image: url("/assets/img/hero.png");
        .scene {
            position: absolute;
            width: 100%;
            bottom: 0;
            transform: translateY(var(--translateY));
            will-change: transform;
        }
    }
    &__scroll {
        position: absolute;
        bottom: 16px;
        color: white;
        left: 0;
        margin: 0 auto;
        font-weight: bold;
        display: block;
        right: 0;
        cursor: pointer;
        width: 120px;
        img {
            width: 40%;
        }
    }
}

