.history {
    position: relative;
    padding: 32px 0;
    color: black;
    h2 {
        position: relative;
        z-index: 9;
    }
    &__bg {
        position: absolute;
        top: 0;
        left: 0;
        right: 0;
        bottom: 0;
        &-timeline {
            position: absolute;
            bottom: 24px;
            left: 24px;
            width: 255px;
            height: 116px;
            img {
                width: 100%;
                height: 100%;
                object-fit: contain;
                opacity: 1;
            }
        }
        img {
            width: 100%;
            height: 100%;
            opacity: .7;
            object-fit: cover;
        }
    }
    &__timeline {
        width: 80%;
        padding: 50px 0;
        margin: 50px auto;
        position: relative;
        overflow: hidden;
        &::before {
            content: '';
            position: absolute;
            top: 0;
            left: 50%;
            margin-left: -1px;
            width: 4px;
            height: 100%;
            background: #000;
            z-index: 1;
            @include breakpoint (mobile) {
                left: 8px;
                width: 2px;
            }
        }
        .marker {
            width: 48px;
            height: 2px;
            // border-radius: 50%;
            border: 1.5px solid black;
            background: black;
            margin-top: 10px;
            z-index: 4;
        }
        .timeline {
            &-block {
                width: -webkit-calc(50% + 8px);
                width: -moz-calc(50% + 8px);
                width: calc(50% + 8px);
                display: -webkit-box;
                display: -webkit-flex;
                display: -moz-box;
                display: flex;
                margin-bottom: 24px;
                -webkit-box-pack: justify;
                -webkit-justify-content: space-between;
                -moz-box-pack: justify;
                justify-content: space-between;
                clear: both;
                @include breakpoint (mobile) {
                    width: 100%;
                    margin-bottom: 30px;
                }
                &-right {
                    float: right;
                    position: relative;
                    @include breakpoint (mobile) {
                        float: none;
                    }
                    &:after {
                        content: '';
                        width: 16px;
                        color: white;
                        position: absolute;
                        height: 16px;
                        background: white;
                        border: 3px solid black;
                        border-radius: 50%;
                        left: 44px;
                        top: 4px;
                    }
                }
                &-left {
                    float: left;
                    direction: rtl;
                    position: relative;
                    @include breakpoint (mobile) {
                        float: none;
                        direction: ltr;
                    }
                    &:before {
                        content: '';
                        width: 16px;
                        top: 3px;
                        color: white;
                        position: absolute;
                        height: 16px;
                        border: 3px solid black;
                        border-radius: 50%;
                        right: 44px;
                        @include breakpoint (mobile) {
                            right: initial;
                            left: 43px;
                        }
                    }
                }
            }
            &-content {
                width: 95%;
                padding: 0 15px;
                color: #666;
                display: flex;
                @include breakpoint (mobile) {
                    width: 85%;
                    margin-bottom: 12px;
                }
                .wrap {
                    margin: -16px 0 0 8px;
                    cursor: pointer;
                }
                &__bullet {
                    width: 66px;
                    display: inline-block;
                    font-size: 20px;
                    line-height: 66px;
                    height: 66px;
                    border-radius: 50%;
                    color: white;
                    text-align: center;
                    box-sizing: border-box;
                }
                &__desc {
                    display: inline-block;
                    width: 360px;
                    margin: 0 8px;
                    text-align: initial;
                    font-weight: bold;
                    vertical-align: middle;
                    @include breakpoint (mobile) {
                        width: 55%;
                        vertical-align: top;
                    }
                }
                h3 {
                    margin-top: 5px;
                    margin-bottom: 5px;
                    font-size: 25px;
                    font-weight: 500;
                }
                span {
                    font-size: 15px;
                    color: #a4a4a4;
                }
                p {
                    font-size: 14px;
                    line-height: 1.5em;
                    word-spacing: 1px;
                    color: #888;
                }
            }
        }
    }
}

.prestasi {
