        }
        .z2 {
            z-index: 2;
            opacity: .5;
        }
        img {
            width: 100%;
            height: 100%;
            object-fit: cover;
        }
    }
    &__menteri {
        position: relative;
        z-index: 4;
        h2 {
            width: 400px;
            text-align: center;
            margin: 0 auto 32px;
            @include breakpoint (mobile) {
                width: 320px;
            }
        }
        .card {
            box-shadow: 0px 5px 10px 0px #bbb;
            border-radius: 25px;
            margin: 0 8px;
            padding-right: 12px;
            display: block;
            @include breakpoint (mobile) {
                margin: 10px;
                padding-right: 0;
            }
            &:focus {
                outline: 0;
                border: 1px solid transparent;
            }
            &__desc {
                margin-left: 8px;
                display: inline-block;
                width: 49%;
                vertical-align: top;
                @include breakpoint (mobile) {
                    margin: 16px;
                    padding-right: 6px;
                    width: 92%;
                    height: 300px;
                }
                .sticky {
                    top: 0;
                    position: sticky;
                    // background: transparent;
                    padding-bottom: 4px;
                    z-index: 99;
                    border-radius: 0 25px 0 0;
                }
                .nama {
                    font-size: 21px;
                    padding-top: 8px;
                    @include breakpoint (mobile) {
                        font-size: 18px;
                    }
                }
                .tahun {
                    @include breakpoint(mobile) {
                        font-size: 16px;
                    }
                }
                .paragrapgh {
                    height: 220px;
                    overflow-y: auto;
                    padding-right: 32px;
                    padding-bottom: 16px;
                    @include breakpoint (mobile) {
                        height: 155px;
                    }
                    &::-webkit-scrollbar-track {
                        // -webkit-box-shadow: inset 0 0 6px rgba(0, 0, 0, 0.3);
                        border-radius: 10px;
                        background-color: #F5F5F5;
                    }
                    &::-webkit-scrollbar {
                        width: 6px;
                        background-color: #F5F5F5;
                    }
                    &::-webkit-scrollbar-thumb {
                        border-radius: 10px;
                        // -webkit-box-shadow: inset 0 0 6px rgba(0, 0, 0, .3);
                        background: #F5886B;
                    }
                }
            }
            &__image {
                display: inline-block;
                width: 49%;
                position: relative;
                height: 100%;
                &--name {
                    position: absolute;
                    bottom: 8px;
                    left: 8px;
                    right: 8px;
                    background: rgba(black, .7);
                    border-radius: 16px;
                    color: white;
                    text-align: center;
                    font-size: 12px;
                    padding: 4px;
                }
                @include breakpoint (mobile) {
                    height: 220px;
                    width: 100%;
                }
                img {
                    height: 100%;
                    width: 100%;
                    border-radius: 25px 0 0 0;
                    object-fit: cover;
                    @include breakpoint(mobile) {
                        border-radius: 25px 25px 0 0;
                    }
                }
            }
        }
        .line {
            background: #625F5F;
            height: 2px;
            width: 90px;
            margin: 24px 0;
        }
    }
}

