.nav {
    overflow-x: hidden;
    &-link {
        @include breakpoint (mobile) {
            color: white !important;
        }
    }
    &__menu {
        display: block;
        padding: 24px 16px;
        text-align: right;
        margin: 0;
        @include breakpoint (mobile) {
            padding: 24px 16px;
            margin: 0 auto;
            text-align: center;
            position: absolute;
            left: 0;
            right: 0;
            top: 84px;
        }
        li {
            display: inline-block;
            margin-right: 4px;
            .sosmed {
                // background-color: white;
                // padding: 4px 8px;
                // text-align: center;
                // box-shadow: 1px 0 10px 2px #bbb;
                // width: 32px;
                // border-radius: 50%;
                // height: 32px;
                i {
                    color: #4a4a4a;
                    font-size: 20px;
                }
            }
        }
    }
}

.nav-absolute {
    position: absolute;
    top: 0;
    left: 0;
    right: 0;
    z-index: 1;
}

.navbar {
    &-nav {
        @include breakpoint (mobile) {
            padding: 16px;
        }
    }
    &-brand {
        padding-top: 38px !important;
        img {
            // height: 30px;
            padding-top: 5px;
        }
        @include breakpoint(mobile) {
            width: 50px;
            line-height: 38px;
            margin: -92px auto 0;
            display: block;
            height: 50px;
            img {
                width: 100%;
                height: auto;
                object-fit: contain;
            }
        }
    }
    &-collapse {
        @include breakpoint (mobile) {
            background: rgba(coral, .7);
            z-index: 9;
            position: relative;
        }
    }
}

#dropdownMenuButton {
    display: inline-block;
    margin-left: 8px;
    cursor: pointer;
}

.toptop {
    position: absolute;
    left: 0;
    right: 0;
}

.icon-sosmed{
    div{
        background: white;
        width: 34px;
        height: 34px;
        border-radius: 50%;
        text-align: center;
        box-shadow: 0 4px 4px rgba(0,0,0,.25);
    }
    i{
        color: black;
        font-size: 20px;
        padding-top: 8px;
    }
}