.slick {
    &-next,
    &-prev {
        position: absolute;
        top: 50%;
        background-color: transparent;
        opacity: .75;
        // box-shadow: 2px 0 10px 2px #bbb;
        border-radius: 100%;
        z-index: 1;
        width: 60px;
        height: 60px;
        @include breakpoint (mobile) {
            top: 102.5%;
        }
        &:hover,
        &:focus {
            opacity: .75;
            // background-color: white;
        }
        &:before {
            font-family: FontAwesome;
            color: #625F5F;
            padding: 24px;
            font-size: 24px;
        }
    }
    &-prev {
        left: -70px;
        @include breakpoint(mobile) {
            left: 114px;
        }
        &:before {
            content: "\f053";
            position: relative;
            left: -8px;
        }
    }
    &-next {
        right: -70px;
        @include breakpoint(mobile) {
            right: 86px;
        }
        &:before {
            content: "\f054";
        }
    }
    &-dots {
        li {
            button {
                &:before {
                    font-family: fontAwesome;
                    content: '\f10c';
                    font-size: 14px;
                }
            }
        }
        .slick-active {
            button {
                &:before {
                    font-family: fontAwesome;
                    content: '\f111';
                    font-size: 14px;
                }
            }
        }
    }
}

// .slick-dots li.slick-active button:before
.btn {
    &-port {
        outline: none;
        border-color: white;
        box-shadow: none;
    }
}